import React from 'react';
// import Suggestions from "./Suggestions";
import Autosuggest from 'react-autosuggest';
import AutosuggestHighlightMatch from 'autosuggest-highlight/match';
import AutosuggestHighlightParse from 'autosuggest-highlight/parse';
import { Image, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import '../css/AutoSuggest.css';

function getMoviesFromMongoDB(type, context) {
  var url = "https://9db5che6y2.execute-api.us-east-1.amazonaws.com/Prod/retrieveMovies?";
  const title = context.title;
  const query = context.query;
  const number = context.number;
  const genre = context.genre;
  const start = context.start;
  const end = context.end;

  if (type === "find_one") {
    url = url + "type=" + type + "&title=" + title;
  }
  else if (type === "find_many") {
    url = url + "type=" + type + "&query=" + query + "&number=" + number;
  }
  else {
    url = url + "type=" + type + "&genre=" + genre + "&start=" + start + "&end=" + end;
  }

  

  return fetch(url)
        .then(res => res.json())
        .then(
          (result) => {
            // console.log(result);
            return result;
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            alert(error);
          }
         );
}

function escapeRegexCharacters(str) {
  var res = str.trim();
  res = res.replace(/[.*+?^${}()|[\]\\]/g, '');
  return res;
}


function getSuggestionValue(suggestion) {
  return `${suggestion.Title}`;
}

function renderSuggestion(suggestion, { query }) {
  const suggestionText = `${suggestion.Title}`;
  const matches = AutosuggestHighlightMatch(suggestionText, query);
  const parts = AutosuggestHighlightParse(suggestionText, matches);
  // console.log(suggestion);
  return (
    
    <span className={'suggestion-content'}>
      <Link to={{ pathname: process.env.PUBLIC_URL + '/movie-page/' + suggestion.Title, state: {movieInfo: suggestion} }}>
      <Image  
        onError={(e)=>{e.target.onerror = null; e.target.src="https://ws3.sinaimg.cn/large/006tKfTcly1g1lmq3s0v2j30dw0dw74h.jpg"}}
        // variant="top" 
        style={{width: 48, height: 48}} 
        src={suggestion.Poster}  fluid/>
      </Link>
      <Link to={{ pathname: process.env.PUBLIC_URL + '/movie-page/' + suggestion.Title, state: {movieInfo: suggestion} }}>
      <span className="name">
        {
          parts.map((part, index) => {
            const className = part.highlight ? 'highlight' : null;

            return (
              <span className={className} key={index}>{part.text}</span>
            );
          })
        }
      </span>
      </Link>
    </span>
  );
}

class Search extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      value: '',
      suggestions: [],
      selectedMovie: '',
    };    

    this.onChange = this.onChange.bind(this);
    this.onKeyDown = this.onKeyDown.bind(this);
    this.handleGo = this.handleGo.bind(this);
  }

  getSuggestions(data) {
    return data.filter(movie => getSuggestionValue(movie));
  }

  // componentDidMount() {
  //   this.setState({people: peopleConst});
  // }

  handleGo() {
    // console.log("clicked");
    const suggestion = this.state.selectedMovie;
    if (suggestion === '') {
      return;
    }
    this.props.history.push(process.env.PUBLIC_URL + '/movie-page/' + suggestion.Title, {movieInfo: suggestion});
  }

  onSuggestionSelected = (event, { suggestion, suggestionValue, suggestionIndex, sectionIndex, method }) => {
    this.setState({selectedMovie: suggestion});
    if (method === 'enter') {
      // console.log(suggestion);
      this.props.history.push(process.env.PUBLIC_URL + '/movie-page/' + suggestion.Title, {movieInfo: suggestion});
      
    }
  }

  onKeyDown = (event) => {
    if (event.key === 'Enter') {
      event.preventDefault();
    }
  }

  onChange = (event, { newValue, method }) => {
    // getMoviesFromMongoDB("find_many", {"query": newValue, "number": 5})
    //   .then(response => JSON.parse(JSON.parse(response.movie)))
    //   .then(data => {this.setState({people: data}); this.setState({ value: newValue });});
    if (newValue === '') {
      this.setState({selectedMovie: ''});
    }
    this.setState({ value: newValue });
  };

  
  onSuggestionsFetchRequested = ({ value, reason }) => {
    // console.log("value is: " + this.state.value);
    value = escapeRegexCharacters(value);
    
    getMoviesFromMongoDB("find_many", {"query": value, "number": 5})
      .then(response => JSON.parse(JSON.parse(response.movie)))
      .then(data => {this.setState({suggestions: this.getSuggestions(data)});});
    // this.setState({
    //   suggestions: this.getSuggestions(value)
    // });
  };

  onSuggestionsClearRequested = () => {
    this.setState({
      suggestions: []
    });
  };

  

  render() {
    const { value, suggestions } = this.state;
    const inputProps = {
      placeholder: "Type movie name...",
      value,
      // type: 'search',
      onChange: this.onChange,
      onKeyDown: this.onKeyDown,
    };
    const ButtonStyle = styled(Button)`
      margin-left: 5px;
      height:30px;
      vertical-align:center; 
      line-height : 100%;  
    `;
    return (
      <React.Fragment>
        <Autosuggest 
          suggestions={suggestions}
          onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
          onSuggestionsClearRequested={this.onSuggestionsClearRequested}
          onSuggestionSelected={this.onSuggestionSelected}
          getSuggestionValue={getSuggestionValue}
          renderSuggestion={renderSuggestion}
          inputProps={inputProps}
          highlightFirstSuggestion={true} />
        
      </React.Fragment>
    );
  }
}


export default Search
